.root {
    display: flex;
    gap: 10px;
    align-items: center;
}

.icon {
    min-width: 25px;
    min-height: 25px;
}



.high {
    color: var(--red);
}

.preHigh {
    color: var(--orange);
}

.low {
    color: var(--blue);
}

.preLow {
    color: var(--light-blue);
}

.middle {
    color: var(--light-orange);
}

.zero {
    color: var(--white);
}

.cross:hover {
    color: var(--gray)
}

@media screen and (hover: none) {
    .cross:hover {
        color: inherit;
    }
}

.icon:active {
    color: var(--light-black);
}